.buyNecklacePage {
  input {
    padding: 5px 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  form {
    margin: 30px 0;
    :global(.btn) {
      margin-left: 10px;
    }
  }
}

.headline {
  text-align: center;
  margin-top: 46px;
  margin-bottom: 48px;
  padding-left: 28px;
  padding-right: 28px;
}

.orderDetailBox {
    //padding: 48px 0 48px;
    background-color: #fff;
}

.section {
    margin-left: 48px;
    margin-right: 48px;
}

.sectionHeader {
    float: left;
    width: 90%;
    border-bottom: 1px solid #e0e0e0;
    margin-bottom: 10px;
}

.section .sectionHeader .operation {
    padding-top: 10px;
    padding-right: 30px;
    vertical-align: top;
    float: right;
}

.section .sectionHeader .title {
    float: left;
    color: #333;
    font-size: 18px;
    line-height: 20px;
    font-weight: normal;
}

.section .sectionHeader .modify {
    float: left;
    color: #32a0e8;
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
    padding-left: 8px;
}


.sectionBody {
    float: left;
    width: 90%;
}

.section .sectionBody table{
    color: #757575;
}

.section .sectionBody th{
    padding: 3px;
}

.section .sectionBody .process {
    width: 86%;
    margin: 30px auto 0;
}

.section .sectionBody .process ul {
    margin-bottom: 5px;
}

.section .sectionBody .process ul.lineText {
    background: #eee;
    border-radius: 16px;
    padding: 0px;
}

.section .sectionBody .process ul li {
    float: left;
    width: 15%;
    margin-left: 13.3333%;
    font-size: 12px;
    text-align: center;
    list-style: none;
}

.section .sectionBody .process ul li:first-child {
    margin-left: 0!important;
}

.section .sectionBody .process ul.lineText li {
    font-size: 14px;
    color: #999;
    line-height: 32px;
    text-align: center;

    &.active {
        color: #fff;
        background: #00c3f5;
        border-radius: 16px;
    }
}
.section .sectionBody .status {
    margin-top: 25px;
}

.section .sectionBody .status .goPay {
    width: 95%;
    padding: 20px 2.5%;
    background: #fef2e8;
    border: 1px solid #ea5245;
    margin-bottom: 20px;
}

.section .sectionBody .status .left {
    float: left!important;
}

.section .sectionBody .status .right {
    float: right!important;
}

.shoppingList {
    padding: 0 0 0 0px;
    font-size: 14px;
}

.shoppingList .title {
    height: 38px;
    padding: 0 24px;
    //background: #E6E7E8;
    border-bottom: 1px solid #E1E1E1;
    line-height: 38px;
    color: #666;
}

.shoppingList .title span{
    float: right;
    text-align: center;
}

.shoppingList .title .name {
    float: left;
    text-align: left;
}

.shoppingList .title .subtotal {
    width: 152px;
    padding-right: 30px;
    text-align: right;
}

.shoppingList .title .num {
    width: 152px;
    padding-right: 30px;
    text-align: right;
}

.shoppingList .title .price {
    width: 152px;
    padding-right: 30px;
    text-align: right;
}

.shoppingList .items {
    padding: 0 24px;
    border-bottom: 1px solid #E1E1E1;
}

.shoppingList .item {
    height: 100px;
}

.shoppingList .item .itemThump {
    margin-top: 10px;
    //border: 1px solid #F0F0F0;
    width: 78px;
    height: 78px;
    border-radius: 4px;
    float: left;
}

.shoppingList .item .itemDesc {
    width: 315px;
    margin-left: 21px;
    line-height: 90px;
    float: left;
}

.shoppingList .item span{
    float: right;
    text-align: center;
}

.shoppingList .item .subtotal {
    width: 152px;
    padding-right: 30px;
    line-height: 90px;
    text-align: right;
}

.shoppingList .item .num {
    width: 152px;
    padding-right: 30px;
    line-height: 90px;
    text-align: right;
}

.shoppingList .item .price {
    width: 152px;
    padding-right: 30px;
    line-height: 90px;
    text-align: right;
}

.shoppingList .summary {
    padding: 0 24px;
    border-bottom: 1px solid #E1E1E1;
    height: 120px;
}

.shoppingList .summary .total{
    width: 152px;
    padding-right: 30px;
    line-height: 90px;
    text-align: right;
    float: right;
}




